*{margin:0;padding:0;font-family:"KIMM","SpaceGrotesk",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-size:inherit;word-break:break-all}html,body{width:100%;height:100%;color:#171717}body{background-color:#f7f8f9}#wrapper{min-height:100vh;background-color:#fff}h1,h2,h3,h4,p{margin:0}ul,ol,li,dl,dt,dd{list-style:none;margin:0;padding:0}button{padding:0;border:0;background-color:rgba(0,0,0,0);cursor:pointer}a{color:inherit;text-decoration:none}#container{position:relative;height:100%;min-height:calc(100vh - 50px);padding:0;box-sizing:border-box;background-color:#fff}#container:after{content:"";clear:both;display:block;height:0;visibility:hidden}.no-scroll{height:100%;min-height:100%;overflow:hidden !important;touch-action:none}@media(min-width: 376px){#wrapper{width:375px;margin:0 auto;box-shadow:0px 0px 7px 0px rgba(113,118,130,.4)}}#header{display:block;position:relative;width:100%;height:50px;padding:10px 20px;box-sizing:border-box}#header::after{content:"";clear:both;display:block;height:0;visibility:hidden}#header h1{display:none;float:left;margin-left:12px;font-size:18px;font-weight:300;line-height:30px;color:#171717}#header .header-logo{display:block;width:50px;height:30px}#header .header-logo img{display:block;width:auto;height:100%}#header .header-back{float:left;width:30px;height:30px;background-image:url("../images/header_back.png");background-repeat:no-repeat;background-size:auto 10px;background-position:left center}#header .header-back+h1{display:block}#header .header-back~.header-logo{display:none}#error{position:absolute;top:50%;left:50%;width:100%;padding:0 30px;box-sizing:border-box;transform:translate(-50%, -50%);text-align:center}#error h1{display:block;width:160px;margin:0 auto 20px;background-color:#fff;font-size:58px;color:#fa7d38}#error strong{display:block;font-size:24px;text-align:center}#error p{margin-top:10px;color:#888}#error .btn{margin-top:20px}#error .zero{position:relative}#error .zero .eye1,#error .zero .eye2{display:block;position:absolute;top:50%;left:50%;transform:translateY(-10px);width:4px;height:7px;border-radius:999px;background-color:#fa7d38}#error .zero .eye1{transform:translate(-7px, -10px)}#error .zero .eye2{transform:translate(3px, -10px)}#error .zero .lips{position:absolute;width:6px;height:4px;border:4px solid #fa7d38;border-bottom:0;border-top-left-radius:7px;border-top-right-radius:7px;top:50%;left:calc(50% - 7px);transform:translateY(-1px)}.intro-btn-group{width:100%;padding:0 40px 50px;box-sizing:border-box}.intro-btn-group .btn+.btn{margin-top:8px}#popup{display:none}#popup .popup-dim{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.4);z-index:301}#popup .popup-wrap{position:absolute;top:50%;left:50%;width:calc(100% - 40px);border-radius:5px;background-color:#fff;transform:translate(-50%, -50%);z-index:302}#popup .popup-header{padding:20px 20px 0}#popup .popup-header .popup-title{display:block;width:calc(100% - 23px);font-size:18px;line-height:18px;white-space:pre-wrap}#popup .popup-close{position:absolute;top:20px;right:20px;width:18px;height:18px}#popup .popup-close:before{content:"";position:absolute;top:50%;left:calc(50% - 1px);width:1px;height:14px;border-left:1px solid #171717;transform:translate(-50%, -50%) rotate(135deg)}#popup .popup-close:after{content:"";position:absolute;top:50%;left:50%;width:1px;height:14px;border-left:1px solid #171717;transform:translate(-50%, -50%) rotate(45deg)}#popup .popup-body{padding:40px 20px 50px;font-size:14px;text-align:center;line-height:18px}#popup .popup-btn-group{display:flex;justify-content:center;padding:0 20px 20px;box-sizing:border-box;gap:15px}#popup .popup-btn-group .btn{max-width:50%;height:40px;border-radius:999px;font-size:14px;line-height:40px}#popup .popup-btn-group .btn-cancel{background-color:#f1f1f1;border-color:#f1f1f1;color:#686868}#popup.style02 .popup-wrap{border-radius:20px;background-color:#3f34c0;color:#fff}#popup.style02 .popup-header{padding-top:40px}#popup.style02 .popup-body{padding-bottom:32px;text-align:left}#popup.style02 .popup-btn-group .btn{height:50px;border:1px solid #fff;background-color:#3f34c0;font-size:14px;line-height:48px}#popup.style02 .popup-btn-group .btn+.btn{background-color:#fff;color:#3f34c0}#popup.style03{display:block}#popup.style03 .popup-wrap{border-radius:20px}#popup.style03 .popup-title{line-height:26px}#popup.style03 .popup-close{height:26px}#popup.style03 .popup-body{padding:8px 20px 24px;text-align:left}#popup.style03 .popup-btn-group{justify-content:right}#popup.style03 .popup-btn-group .btn{width:auto;min-width:70px;background-color:#6558f5;border-color:#6558f5;color:#fff}@media(min-width: 376px){#popup .popup-dim{width:375px;left:50%;transform:translateX(-50%)}}#layer{display:none}#layer .layer-wrap{position:fixed;top:0;left:0;width:100%;height:100%;background-color:#fff;z-index:301}#layer .layer-header{display:block;position:relative;width:100%;height:50px;padding:10px 20px;box-sizing:border-box}#layer .layer-header::after{content:"";clear:both;display:block;height:0;visibility:hidden}#layer .layer-title{float:left;margin-left:12px;font-size:18px;font-weight:300;line-height:30px;color:#171717}#layer .layer-close{float:left;width:30px;height:30px;background-image:url("../images/header_back.png");background-repeat:no-repeat;background-size:auto 10px;background-position:left center}#layer .layer-body{display:block;width:100%;height:calc(100vh - 170px);padding:10px 20px 40px;box-sizing:border-box;overflow-y:auto}#layer .layer-btn{padding:40px 20px 20px}@media(min-width: 376px){#layer .layer-wrap{width:375px;left:50%;transform:translateX(-50%)}}#policy h3{display:block;margin-bottom:36px;font-size:16px;font-weight:300;line-height:18px;color:#fa7d38}#policy strong{display:block;font-size:14px;font-weight:700;line-height:18px}#policy .area{margin-top:36px}#policy .area .title{margin-bottom:4px}#policy .area p{font-size:12px;font-weight:300;line-height:14px}#policy *+h3{margin-top:36px}